 .aroundCanvasR{
    position: absolute;
    text-align: center;
    top:0;
    bottom:0;
    left:0;
    right:0;
    width:800px;
    height:500px;
    overflow: hidden;
 }

 .samePosR{
    display:grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    background-color:black;
    border: 1px solid rgb(56, 56, 56);
    width:800px;
    margin:auto;
    color:gray;
    font-size: larger;
    font-weight: bold;
    cursor: pointer;
 }

 .restart1R{
    color:gray;
    
 }

 .restart2R{
    color:gray;
    
 }

 .exitR{
    color:gray;
    
 }

.tooltip-textR{
    visibility: hidden;
    position: absolute;
    z-index:1;
    width:600px;
    color:black;
    font-size: 20px;
    background-color: rgb(0, 225, 255);
    border-radius: 5px;
    padding:5px;
    top:25px; 
    text-align: left;
}

.tooltip-text-desktopR{
    visibility: hidden;
    position: absolute;
    z-index:1;
    width:600px;
    color:black;
    font-size: 25px;
    background-color: rgb(0, 225, 255);
    border-radius: 5px;
    padding:5px;
    top:25px; 
    text-align: left;
}


@media screen and (min-width: 1300px) {  
    .aroundCanvasR{
        margin: auto;
        overflow:scroll;
     }
    
    .infoR:hover .tooltip-text-desktopR{
        visibility: visible;
    }
}

@media screen and (min-width: 810px) {  
    .aroundCanvasR{
        margin: auto;
     }
}


/* =============== MEDIA QUERIES (MEDIUM DEVICES) ============== */
@media screen and (max-width: 1280px) {


    .infoR:hover .tooltip-textR{
        visibility: visible;
    }
    
    .infoR .tooltip-textR:hover{
       display: none;
    }

}

  