.startScreen{ 
    position: absolute;
    background-color: black;
    align-items: center;
    width: 100vw;
    height:100vh;
}

.startBody{
   
    text-align: center;
    width:80%;
    margin:10%;
    padding: 2%;
    background-color:rgb(189, 211, 230);
}


.gameTitle{
    
    font-size: 200px;
    font-weight: bold;
}

.button{
    background-color: rgb(64, 52, 231);
    width:10%;
    margin-top: 5%;
    margin-bottom: 5%;
    margin-left: 45%;
    text-align: center;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    color: white;
}

body {
    margin:0;
    padding:0;
    
 }

 .aroundCanvas{
    position: absolute;
    background-color:black;
    text-align: center;
    top:0;
    bottom:0;
    left:0;
    right:0;
 }

 .samePos{
    display:grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    width:26%;
    margin-left: 37%;
    margin-right: 37%;
    color:gray;
    font-size: larger;
    font-weight: bold;
    cursor: pointer;
 }

 .restart1{
    color:gray;
    
 }

 .restart2{
    color:gray;
    
 }

 .exit{
    color:gray;
    
 }

.portraitWindow{
    padding:0;
    width:100%;
    margin-bottom:3%;
    background-color: rgb(22, 41, 56);
}

.landscapeWindow{
    padding:0;
    background-color: rgb(22, 41, 56);
    width:26%;
    margin-left: 37%;
    margin-right: 37%;
}

.tooltip-textb{
    visibility: hidden;
    position: absolute;
    z-index:1;
    width:250px;
    color:red;
    font-size: 14px;
    background-color: black;
    border-radius: 5px;
    padding:5px;
    top:25px; 
    text-align: left;
}

.tooltip-text-desktopb{
    visibility: hidden;
    position: absolute;
    z-index:1;
    width:300px;
    color:red;
    font-size: 20px;
    background-color: black;
    border-radius: 5px;
    padding:5px;
    top:25px; 
    text-align: left;
}



@media screen and (min-width: 1300px) {  
    .infob:hover .tooltip-text-desktopb{
        visibility: visible;
    }
}

/* =============== MEDIA QUERIES (MEDIUM DEVICES) ============== */
@media screen and (max-width: 1280px) {


    .infob:hover .tooltip-textb{
        visibility: visible;
    }
    
    .infob .tooltip-textb:hover{
       display: none;
    }


    .portraitWindow{
        padding:0;
        width:70%;
        margin-left: 15%;
        margin-right: 15%;
        
    }
    
    .landscapeWindow{
        padding:0;
        width:32%;
        margin-left: 34%;
        margin-right: 34%;
       
    }

    .samePos{
        width:32%;
    margin-left: 34%;
    margin-right: 34%;
     }

}

